<template>
    <main class="hide-overflow">
        <div class="title"> <!-- title de présentation, dans le main -->
                <h1>Inscription</h1>
            </div>
            <div class="card content"><!-- intro -->
                <h2>Pourquoi s'inscrire?</h2>
                <p>
                    S'inscrire permet d'ajouter du contenu au site. Vous pouvez apporter aux biographies vos recherches et les informations manquantes.
                    <br>Du coté des fiches de révisions, vous pouvez mettre en commun votre travail ou encore prendre la relève des élèves de première lorsqu'ils passeront leur oral de français.
                </p>
                <br>
                <h2 id="inscription">Conditions d'inscription</h2>
                <p>
                    En vous inscrivant vous:
                    <ul>
                        <li>acceptez notre <b><a href="/res/rgpd.pdf">politique d'utilisation des données personnelles</a></b></li>
                        <li>reconnaissez votre motivation</li>
                        <li>vous <b>engagez</b> à respecter le contenu déjà présent tout en garantissant que celui que vous apporterez est <b>sérieux et de qualité.</b></li>
                    </ul>
                    Il est important de comprendre la responsabilité qu'engage cette inscription, si elle vient à être validée. 
                    <br>En effet, les fiches de révisions étant destinées à des étudiants, il va de soi que le contenu apporté doit être soigné et cohérent: il est important de suivre le même schéma sur chaque fiche.
                    Les biographies nécessitent elles aussi une certaine rigueur.
                </p>
                <h3>Informations</h3>
                <p>
                    Une fois votre compte créé, nous entrerons en contact avec vous via l'adresse email ou le numéro de téléphone fourni.
                    <br>Ce contact, éventuellement suivi par une rencontre en face à face au lycée nous permettra de faire connaissance, mais aussi de juger votre motivation et votre sérieux.
                </p>
                <p>
                    Si vous n'êtes pas lycéen en première ou encore professeur il sera difficile pour vous d'apporter du contenu pour des fiches de révisions sur les auteurs actuellement vu en cours. 
                    <br>Cependant, vous pouvez intégrer l'équipe des rédacteurs pour compléter la bibliographie et y apporter vos connaissances. Faites la demande si vous êtes motivé, nous verrons cela avec vous par la suite.
                </p>
                <br>
        </div>
        <div class="auth">
            <form>
                <h1>Créer un compte</h1>
                <p>En créant un compte vous acceptez les conditions d'inscription ci-dessus et notre <b><a href="/res/rgpd.pdf">politique RGPD</a></b>. Merci de les <b>lire attentivement avant de vous inscrire</b>.</p>
                <br>
                <label v-html="nameLabel"></label>
                <input type="text" v-model="name" id="name" placeholder="Votre nom ou prénom" @keyup="verifyInputs"><br>
                <label v-html="mdpLabel"></label> 
                <input :type="showPwd?'text':'password'" v-model="password" id="password" placeholder="Minimum 8 caractères" @keyup="verifyInputs">
                <input :type="showPwd?'text':'password'" v-model="cpassword" placeholder="Confirmez le mot de passe" @keyup="verifyInputs">
                <sup style="cursor:pointer; text-align:right; margin-right:5px" @click="showPwd = !showPwd">{{showPwd?'Cacher le mot de passe':'Afficher le mot de passe'}}</sup><br>
                <label v-html="contactLabel"></label>
                <input type="text" v-model="contact" id="contact" placeholder="Nécessaire pour vous contacter" @keyup="verifyInputs" @keyup.enter="postRegister">
                <br>
                <input type="button" value="Créer un compte" @click="postRegister">
                <BarLoader :height=2 :width=100 v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
                <sup style="text-align:center"><a href="/login">Se connecter</a></sup>
            </form>
        </div>
    </main>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import axios from 'axios'


export default {
    data () {
        return {
            nameLabel: "Nom d'utilisateur pour la connexion",
            contactLabel: "Mail ou numéro de téléphone",
            mdpLabel: "Mot de passe",
            name: '',
            password: '',
            cpassword: '',
            showPwd: false,
            contact: '',
            loading: false
        }
    },
    computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('api',[
        'apiPath'
        ])
    },
    methods: {
        ...mapActions('jwt',[
      `setJwt`,
        ]),
        ...mapActions('alert',[
            'newAlert'
        ]),
        verifyInputs () {
            if (this.name == '') {
                this.nameLabel = '<span style="color:#FF3535">Nom d\'utilisateur pour la connexion requis</span>'
                return false
            } else {
                this.nameLabel = 'Nom d\'utilisateur pour la connexion'
            }
            if( this.password == '' || this.password.length < 8 || !this.password.match(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,50}$/)) {
                this.mdpLabel = '<span style="color:#FF3535">Mot de passe complexe requis <br><sub>minimum 8 caractères, contenant au moins majuscule, minuscule et chiffre</sub></span>'
                return false
            } else if (this.password !== this.cpassword) {
                this.mdpLabel = '<span style="color:#FF3535">Mots de passe différents</span>'
                return false
            } else {
                this.mdpLabel = "Mot de passe"
            }
            if (this.contact == '') {
                this.contactLabel = '<span style="color:#FF3535">Mail ou numéro de téléphone requis</span>'
                return false
            } else {
                this.contactLabel = 'Mail ou numéro de téléphone'
            }

            return true
        },
        // Pushes posts to the server when called.
        postRegister () {
            if (!this.verifyInputs()) return false
            this.loading = true
            axios({
                method: 'post',
                url: `${this.apiPath}/api/register`,
                headers: {
                    Authorization: `Bearer ${this.jwt}`
                },
                data: {
                    name: this.name,
                    password: this.password,
                    contact: this.contact
                }
            })
                .then(response => {
                    this.loading = false
                if (response.data.status == 'success') {
                    this.newAlert({type:'success', content:'Account successfully created'})
                    this.newAlert({type:'warning', content:"Votre compte n'étant pas validé, vous ne pouvez pas accéder à l'interface d'administration"})
                }
                if (response.data.token) {
                    this.setJwt(response.data.token)
                    this.$router.push('/')
                }
                if (response.data.status == 'error') {
                    let error = response.data.error.password ? response.data.error.password: (response.data.error.name ? response.data.error.name:response.data.error)
                    this.newAlert({type:'error', content:error})
                }
                })
                .catch(e => {
                    this.newAlert({type:'error', content:e})
                })
        }
    },
    created () {
        window.scrollTo(0, 0)
    }
}
</script>
